@import url("https://fonts.googleapis.com/css?family=Play");
@import url("https://fonts.googleapis.com/css?family=Roboto");
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif; }

::-webkit-scrollbar {
  display: none; }

h1,
h2,
h3,
h4,
h5,
h6,
strong {
  font-family: "Play", sans-serif;
  color: #a219c4; }

.app {
  color: #2E2E2E;
  margin: 0px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 100vh 1fr 200px;
  grid-template-areas: "logo" "content" "footer";
  height: 100vh;
  background-color: white; }

header.logo {
  grid-area: logo; }

main.content {
  grid-area: content; }

footer.footer {
  grid-area: footer; }

footer.footer {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding: 0 25px;
  background-color: white;
  box-shadow: 0 2px 23px 0 rgba(0, 0, 0, 0.1), 0 2px 49px 0 rgba(0, 0, 0, 0.06); }

main > div {
  background-color: white; }

footer ul {
  list-style-type: none;
  padding-left: 0%; }

a a:hover {
  text-decoration: none; }

.recuo {
  text-indent: 4em; }

@media (max-width: 768px) {
  .app {
    grid-template-rows: 60vh 1fr 480px; }
  footer.footer {
    margin-top: 1%;
    margin-bottom: 1%;
    padding: 3%;
    height: 480px;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start; } }

header.logo {
  background: -webkit-linear-gradient(right, #e535ab, #a219c4, #574bfd, #a4dfbc);
  background: linear-gradient(270deg, #e535ab, #a219c4, #574bfd, #a4dfbc);
  background-size: 800% 800%;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  width: 100%;
  height: 100vh;
  -webkit-animation: AnimationName 17s ease infinite;
  animation: AnimationName 17s ease infinite; }

@-webkit-keyframes AnimationName {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes AnimationName {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

header.logo img {
  width: 60%; }

@media (max-width: 768px) {
  header.logo img {
    width: 90%; }
  header.logo {
    height: 60vh; } }
