@import url("https://fonts.googleapis.com/css?family=Play");
@import url("https://fonts.googleapis.com/css?family=Roboto");
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif; }

::-webkit-scrollbar {
  display: none; }

h1,
h2,
h3,
h4,
h5,
h6,
strong {
  font-family: "Play", sans-serif;
  color: #a219c4; }

.app {
  color: #2E2E2E;
  margin: 0px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 100vh 1fr 200px;
  grid-template-areas: "logo" "content" "footer";
  height: 100vh;
  background-color: white; }

header.logo {
  grid-area: logo; }

main.content {
  grid-area: content; }

footer.footer {
  grid-area: footer; }

footer.footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 25px;
  background-color: white;
  box-shadow: 0 2px 23px 0 rgba(0, 0, 0, 0.1), 0 2px 49px 0 rgba(0, 0, 0, 0.06); }

main > div {
  background-color: white; }

footer ul {
  list-style-type: none;
  padding-left: 0%; }

a a:hover {
  text-decoration: none; }

.recuo {
  text-indent: 4em; }

@media (max-width: 768px) {
  .app {
    grid-template-rows: 60vh 1fr 480px; }
  footer.footer {
    margin-top: 1%;
    margin-bottom: 1%;
    padding: 3%;
    height: 480px;
    justify-content: flex-start;
    align-items: flex-start; } }

.cardp {
  margin: 7px;
  padding: 0.5%; }

.cardp:hover .card-img-top {
  -webkit-filter: grayscale(0%);
  filter: grayscale(0%);
  animation-name: fade;
  animation-duration: .6s;
  opacity: 1; }

.card-img-top {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
  opacity: 0.7;
  width: 100%; }

@keyframes fade {
  from {
    -webkit-filter: grayscale(100%);
    filter: grayscale(100%);
    opacity: 0.7; }
  to {
    -webkit-filter: grayscale(0%);
    filter: grayscale(0%);
    opacity: 1; } }

.card-body {
  opacity: 1;
  text-shadow: 0 2px 23px 0 rgba(0, 0, 0, 0.1), 0 2px 49px 0 rgba(0, 0, 0, 0.06); }

@media (max-width: 576px) {
  .card-img-top {
    -webkit-filter: grayscale(0%);
    filter: grayscale(0%);
    opacity: 1; } }
